import Vue from "vue"
import VeeValidate, {Validator} from  'vee-validate'
import zh_CN from 'vee-validate/dist/locale/zh_CN';//

//
Validator.addLocale(zh_CN);

const config = {
  locale: 'zh_CN'
};

Vue.use(VeeValidate, config);

//validate
const dictionary = {
  zh_CN: {
    message: {
      email: () => '',
      required: (field) => "" + field
    },
    attributes: {
      email: '',
      passWord: '',
      name: "",
      userName: '',
      phone:'',
      validateCode:'',
      newPassword:'',
      newPassword2:'',
      mobile:'',
      mailBox:''

    }
  }
};

Validator.updateDictionary(dictionary);

//
Validator.extend('mobile', {
  messages: {
    zh_CN: field => field + '',
  },
  validate: value => {
    return value.length == 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/.test(value)
  }
});

//
Validator.extend('special', {
  messages: {
    zh_CN: field => '',
  },
  validate: value => {
    return !(/[`~!@#$%^&*()_+<>?:"{},\/;'[\] ]/).test(value) && !(/[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/ig).test(value)
  }
});

//
Validator.extend('mailBox', {
  messages: {
    en: (field, args) => {
      return field + ""
    },
    cn: (field, args) => {
      return field + ""
    },
    zh_CN: (field, args) => {
      return field + ""
    }
  },
  validate: value => {
    return (/^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g).test(value)
  }
});

//
Validator.extend('required', {
  messages: {
    en: (field, args) => {
      return field + ""
    },
    cn: (field, args) => {
      return field + ""
    },
    zh_CN: (field, args) => {
      return field + ""
    }
  },
  validate: value => {
    return value.length > 0;
  }
});

//6-20[A-Za-z0-9_]
Validator.extend('passw', {
  messages: {
    en: (field, args) => {
      return field + "6-20"
    },
    cn: (field, args) => {
      return field + "6-20"
    },
    zh_CN: (field, args) => {
      return field + "6-20"
    }
  },
  validate: value => {
    return /^(\w){6,20}$/.test(value)
  }
});

//
Validator.extend('nameSpace', {
  messages: {
    en: (field, args) => {
      return field + ""
    },
    cn: (field, args) => {
      return field + ""
    },
    zh_CN: (field, args) => {
      return field + ""
    }
  },
  validate: value => {
    return !/(^\s+)|(\s+$)/g.test(value)
  }
});

//4
Validator.extend('num_length', {
  messages: {
    en: field => '4',
    cn: field => '4',
    zh_CN: field => '4',
  },
  validate: (value,args) => {
    let len = 0;
    for (let i = 0; i < value.length; i++) {
      if (value[i].match(/[^\x00-\xff]/ig) !== null){
        len += 2;
      }
      else{
        len += 1;
      }
    }
    return len === parseInt(args[0]);
  }
});




