<template>
  <div id="baseInfo" @click="hiddenSelect">
    <section class="viewItem medical-record-form-item ">
      <form action="">
        <section class="base-info medical-record-main">
          <article>
            <ul>
              <li><span class="base-title"></span><span class="base-text">{{address}}</span></li>
              <li v-if="false"><span class="base-title"></span><span class="base-text">{{telephone}}</span></li>
              <li><span class="base-title"></span><span class="base-text" :data-value="socialId.value">{{socialId.text}}</span></li>
              <li><span class="base-title base-title-long"></span><span class="base-text">{{socialAddress}}</span></li>
              <li><span class="base-title"></span><select class="" v-model="nationDataSelectValue">
                <option v-for="option in nation" v-bind:value="option.nationality">
                  {{option.nationality}}
                </option>
              </select><span class="base-title ml20"></span><select class="" v-model="marriageSelectValue">
                <option value=""></option>
                <option value="1"></option>
                <option value="2"></option>
                <option value="3"></option>
                <option value="4"></option>
              </select>
              </li>
              <li>
                <span class="base-title "></span><address-selector  :dataListInfo.sync ="addressResult" :conIndex="1" :currentIndexNow.sync="currentSelectorIndex" :dataBack.sync="addressResult" v-if="childrenShow"></address-selector>
              </li>
              <li>
                <span class="base-title"></span><input class="base-input" type="text" maxlength="50" placeholder="" v-model="homeAddress"/>
              </li>
              <li>
                <span class="base-title"></span><input class="base-input" type="text" maxlength="50" placeholder="" v-model="workplace"/>
              </li>
            </ul>
          </article>
          <article>
            <header>
              <h2></h2>
            </header>
            <section>
              <ul>
                <li>
                  <span class="base-title"></span><address-selector  :dataListInfo.sync ="birthResult"  :dataBack.sync="birthResult" :conIndex="2" :currentIndexNow.sync="currentSelectorIndex" v-if="childrenShow"></address-selector>
                </li>
                <li>
                  <span class="base-title"></span><select class="select-120" v-model="fertilityBoySelectValue">
                      <option value=""></option>
                      <option value="0">0</option>
                      <option value="1">1</option>
                      <option value="2">2</option>
                      <option value="3">3</option>
                      <option value="4">4</option>
                      <option value="5">5</option>
                      <option value="6">6</option>
                      <option value="7">7</option>
                      <option value="8">8</option>
                    </select><select class="select-120 ml15" v-model="fertilityGirlSelectValue">
                      <option value=""></option>
                      <option value="0">0</option>
                      <option value="1">1</option>
                      <option value="2">2</option>
                      <option value="3">3</option>
                      <option value="4">4</option>
                      <option value="5">5</option>
                      <option value="6">6</option>
                      <option value="7">7</option>
                      <option value="8">8</option>
                    </select>
                </li>
                <li>
                  <span class="base-title"></span><select class="" v-model="spouseStatusSelectValue">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                </select><span class="base-title ml20"></span><select class="" v-model="childrenStatusSelectValue">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                </select>
                </li>
                <li>
                  <span class="base-title"></span><select class="" v-model="marriageAgeSelectValue">
                    <option v-for="option in marriageAge" v-bind:value="option.value">
                      {{option.text}}
                    </option>
                  </select><span class="base-title ml20"></span><select class="" v-model="isSmokeSelectValue">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                </select>
                </li>
                <li>
                  <span class="base-title"></span><select class="" v-model="isDrinkSelectValue">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                </select><span class="base-title ml20"></span><select class="" v-model="isNarcoticsSelectValue">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                </select>
                </li>
              </ul>
            </section>
          </article>
          <article>
            <header>
              <h2></h2>
            </header>
            <section>
              <ul>
                <li>
                  <span class="base-title"></span><select class="" v-model="parentStratusSelectValue">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                </select><span class="base-title ml20"></span><select class="" v-model="siblingsStatusSelectValue">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                </select>
                </li>
                <li>
                  <span class="base-title"></span><select class="" v-model="isInfectionSelectValue">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                  <option value="5"></option>
                  <option value="6"></option>
                </select><span class="base-title ml20"></span><select class="" v-model="isHeredopathiaSelectValue">
                  <option value="0"></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                  <option value="5"></option>
                  <option value="6"></option>
                </select>
                </li>
              </ul>
            </section>
          </article>
          <footer>
            <button type="button" class="detail-saveBtn" @click="saveData()" v-show="$store.state.userListStatus.second && ($store.state.currentItem.consultationState == 6||$store.state.currentItem.consultationState == -1||$store.state.currentItem.consultationState ==0||$store.state.currentItem.consultationState ==9||$store.state.currentItem.consultationState ==10)"></button>
          </footer>
        </section>
      </form>
    </section>
    <transition name="fade-scale">
      <popup v-if="popupShow" :obj.sync="popupObj" :payPopupShow.sync="popupShow"></popup>
    </transition>
  </div>
</template>
<script>
  import Vue from 'vue'
  import api from '@/common/js/util/util';
  import addressSelector from '@/common/addressSelector'
  import popup from  '@/common/popup';

  const XHRList = {
      nationSearch: "/call/comm/data/baseinfo/v1/getNationalityList/",
      getBaseInfo:'/call/customer/patient/baseinfo/v1/getMapList/',
      saveBaseInfo: "/call/customer/patient/baseinfo/v1/save/",
  };
  export default {
    name: 'baseInfo',
    data(){
      return {
        id: '',
        address: '',
        telephone: '',
        socialId: '',
        socialAddress: '',
        marriageAge: [],
        marriageAgeSelectValue: '',
        nation: [],
        nationDataSelectValue: '',
        nationDataSelectName:'',
        marriageSelectValue: '',
        nativeAddress: '',
        homeAddress: '',
        workplace: '',
        fertilityBoySelectValue: '',
        fertilityGirlSelectValue: '',
        spouseStatusSelectValue: '',
        childrenStatusSelectValue: '',
        isSmokeSelectValue: '',
        isDrinkSelectValue: '',
        isNarcoticsSelectValue: '',
        parentStratusSelectValue: '',
        siblingsStatusSelectValue: '',
        isInfectionSelectValue: '',
        isHeredopathiaSelectValue: '',
        userMessage: {},
        provinceList:[],
        cityList:{},
        addressResult:{
          provinceId: '',
          provinceName: '',
          cityId: '',
          cityName: '',
          districtId: '',
          districtName: ''
        },
        birthResult:{
          provinceId: '',
          provinceName: '',
          cityId: '',
          cityName: '',
          districtId: '',
          districtName: ''
        },
        childrenShow:false,
        popupShow:false,
        popupObj: {},
        currentSelectorIndex:1
      }
    },
    props: {
      recordData: {
        type: Object
      }
    },
    components:{
      addressSelector,
      popup
    },
    mounted(){
      this.init();
      //console.log(this.$route.params)
    },
    watch: {
      '$store.state.currentItem'(){
        this.init();
      }
    },
    methods: {
      init() {
        this.childrenShow = false;
        this.userMessage = this.$store.state.currentItem;
        this.addData();
      },
      addData() {
        this.nationGet();  //
        this.baseInfoGet();//
      },
      nationGet() {
        let _this = this;
        let dataValue = {
          "isValid": 1,
          "firstResult": 0,
          "maxResult": 999
        };
        api.ajax({                //
          url: XHRList.nationSearch,
          method: "POST",
          data: dataValue,
          beforeSend(config) {
          },
          done(res) {
            if (res.responseObject.responseData.dataList && res.responseObject.responseStatus == true) {
              let dataList = res.responseObject.responseData.dataList;
              let dataObj = {
                firstResult: 0,
                id: 0,
                ids: {},
                maxResult: {},
                nationality: "",
                page: {},
                pageSize: {},
                sortType: 0
              };
              dataList.unshift(dataObj);
              _this.nation = dataList;
            } else {
              console.log("");
            }
          },
          fail(error){
            console.log("" + error);
          }
        });
      },
      baseInfoGet() {
        let _this = this;
        let dataValue = {
          isValid: "1",
          firstResult: "0",
          maxResult: "1",
          patientId: _this.userMessage.patientId     //ID 
        };
        api.ajax({         //
          url: XHRList.getBaseInfo,
          method: "POST",
          data: dataValue,
          beforeSend(config) {
          },
          done(res) {
            if (res.responseObject.responseData.dataList && res.responseObject.responseStatus == true) {
              let data = res.responseObject.responseData.dataList[0];
              _this.id = data.id;
              //
              _this.address = data.province + " " + data.city + " " + data.district;
              //
              _this.telephone = data.mobile;
              //
              switch (data.socialId) {
                case "0" :
                  _this.socialId = {value: 0, text: ""};
                  break;
                case "1" :
                  _this.socialId = {value: 1, text: ""};
                  break;
                case "2" :
                  _this.socialId = {value: 2, text: ""};
                  break;
                case "3" :
                  _this.socialId = {value: 2, text: ""};
                  break;
                case "4" :
                  this.socialId = {value: 2, text: ""};
                  break;
                default:
                  _this.socialId = {value: data.socialId, text: ""};
              }
              //
              _this.nationDataSelectValue =data.nation?(data.nation == ''?'':data.nation):'';
              //
              _this.marriageSelectValue = data.isMarriage > 0 ? data.isMarriage : "";
              //  data.marriage ? _this.marriageSelectValue = data.marriage : _this.marriageSelectValue = "";
              //
              _this.addressResult.provinceId=data.nativeProvinceId;
              _this.addressResult.provinceName=data.nativeProvince;
              _this.addressResult.cityId=data.nativeCityId;
              _this.addressResult.cityName=data.nativeCity;
              _this.addressResult.districtId=data.nativeDistrictId;
              _this.addressResult.districtName=data.nativeDistrict;

              //
               _this.birthResult.provinceId=data.birthplaceProvinceId;
               _this.birthResult.provinceName=data.birthplaceProvince;
               _this.birthResult.cityId=data.birthplaceCityId;
               _this.birthResult.cityName=data.birthplaceCity;
               _this.birthResult.districtId=data.birthplaceDistrictId;
               _this.birthResult.districtName=data.birthplaceDistrict;

              //  $("#base-info-address").find(".firstListTitle").text(nativeAddress).attr("data-province", data.nativeProvinceId).attr("data-city", data.nativeCityId).attr("data-district", data.nativeDistrictId);                                   //
              //
              data.socialProvince ? _this.socialAddress = data.socialProvince + " " + data.socialCity + " " + data.socialDistrict : _this.socialAddress = "";
              //
              _this.homeAddress = data.homeAddress;
              //
              _this.workplace = data.workplace;
              //
              if (data.fertility.length > 0) {
                let fertilityList = data.fertility.split(",");
                _this.fertilityBoySelectValue = fertilityList[0];
                _this.fertilityGirlSelectValue = fertilityList[1];
              } else {
                _this.fertilityBoySelectValue = "";
                _this.fertilityGirlSelectValue = "";
              }
              //
              data.spouseStatus != '' && data.spouseStatus > 0 ? _this.spouseStatusSelectValue = data.spouseStatus : _this.spouseStatusSelectValue = 0;
              //
              data.childrenStatus != '' && data.childrenStatus > 0 ? _this.childrenStatusSelectValue = data.childrenStatus : _this.childrenStatusSelectValue = 0;
              //

              _this.marriageAge=[];
              _this.marriageAge.push({value: "", text: ""});
              for (let i = 15; i <= 60; i++) {
                _this.marriageAge.push({value: i, text: i});
              }
              data.marriageAge != '' && data.marriageAge > 0 ? _this.marriageAgeSelectValue = data.marriageAge : _this.marriageAgeSelectValue = "";
              //
              data.isSmoke != '' && data.isSmoke > 0 ? _this.isSmokeSelectValue = data.isSmoke : _this.isSmokeSelectValue = 0;
              //
              data.isDrink != '' && data.isDrink > 0 ? _this.isDrinkSelectValue = data.isDrink : _this.isDrinkSelectValue = 0;
              //
              data.isNarcotics != '' && data.isNarcotics > 0 ? _this.isNarcoticsSelectValue = data.isNarcotics : _this.isNarcoticsSelectValue = 0;
              //
              data.parentStatus != '' && data.parentStatus > 0 ? _this.parentStratusSelectValue = data.parentStatus : _this.parentStratusSelectValue = 0;
              //
              data.siblingsStatus != '' && data.siblingsStatus > 0 ? _this.siblingsStatusSelectValue = data.siblingsStatus : _this.siblingsStatusSelectValue = 0;
              //
              data.isInfection != '' && data.isInfection > 0 ? _this.isInfectionSelectValue = data.isInfection : _this.isInfectionSelectValue = 0;
              //
              data.isHeredopathia != '' && data.isHeredopathia > 0 ? _this.isHeredopathiaSelectValue = data.isHeredopathia : _this.isHeredopathiaSelectValue = 0;

//            //
//            $(".J-socialAddress").text(data.socialProvince + " " + data.socialCity + " " + data.socialDistrict).attr("data-province", data.socialProvinceId).attr("data-city", data.socialCityId).attr("data-district", data.socialDistrictId);
//            //
//            $("#base-info-address").find(".firstListTitle").text(nativeAddress).attr("data-province", data.nativeProvinceId).attr("data-city", data.nativeCityId).attr("data-district", data.nativeDistrictId);                                   //
//            //
//            $("#base-info-born-address").find(".firstListTitle").text(birthplaceInfo).attr("data-province", data.birthplaceProvinceId).attr("data-city", data.birthplaceCityId).attr("data-district", data.birthplaceDistrictId);                           //
              _this.childrenShow = true;
            }
          },
          fail(error){
            console.log("" + error);
          }
        });
      },
      saveData() {
        let _this = this;
        let dataValue = {
          nativeProvinceId: this.addressResult.provinceId,   //string		Id
          nativeProvince:  this.addressResult.provinceName,	//string		
          nativeCityId: this.addressResult.cityId,	//string		ID
          nativeCity:  this.addressResult.cityName,//string		
          nativeDistrictId:  this.addressResult.districtId,	//string		
          nativeDistrict: this.addressResult.districtName,	//string		

          birthplaceProvinceId: this.birthResult.provinceId,	//string		id
          birthplaceProvince: this.birthResult.provinceName,	//string		
          birthplaceCityId: this.birthResult.cityId,//string		id
          birthplaceCity: this.birthResult.cityName,	//string		
          birthplaceDistrictId: this.birthResult.districtId,	//string		/id
          birthplaceDistrict:  this.birthResult.districtName,	//string		/

          patientId: this.userMessage.patientId,          //ID 
          id: this.id,
          address: this.address,
          telephone: this.telephone,                      //
          socialId: this.socialId.value,                   //string		
          socialAddress: this.socialAddress,
          nation: this.nationDataSelectValue==''?'': this.nationDataSelectValue,             //
          isMarriage: this.marriageSelectValue,           //
          homeAddress: this.homeAddress,                  //
          workplace: this.workplace,                      //
          spouseStatus: this.spouseStatusSelectValue,     //
          childrenStatus: this.childrenStatusSelectValue, //
          fertility: this.fertilityBoySelectValue + ',' + this.fertilityGirlSelectValue,  //
          marriageAge: this.marriageAgeSelectValue,       //
          isSmoke: this.isSmokeSelectValue,               //
          isDrink: this.isDrinkSelectValue,               //
          isNarcotics: this.isNarcoticsSelectValue,        //
          parentStatus: this.parentStratusSelectValue,    //
          siblingsStatus: this.siblingsStatusSelectValue, //
          isInfection: this.isInfectionSelectValue,       //
          isHeredopathia: this.isHeredopathiaSelectValue  //
        };

        api.ajax({         //
          url: XHRList.saveBaseInfo,
          method: "POST",
          data: dataValue,
          beforeSend(config) {
          },
          done(res){
              _this.baseInfoGet();

            _this.popupShow = true;
            _this.popupObj = {
              text: ''
            }
          },
          fail(error){
            _this.popupShow = true;
            _this.popupObj = {
              text: ''
            };
            console.log("" + error);
          }
         });
      },
      hiddenSelect(){
          this.currentSelectorIndex =-1;
          console.log(this.currentSelectorIndex);
      }
    }
  }
</script>
<style type="text/css" lang="scss" rel="stylesheet/scss" scoped>
  //baseInfo.scss
  @import "../../scss/library/_common-modules";
  @import "../../scss/record_common";
  .base-info {
    .base-title {
      width: 54px;
      font-size: 13px;
      color: #555555;
      letter-spacing: 0;
      line-height: 14px;
      margin-right: 8px;
      text-align: right;
      display: inline-block;
      vertical-align: middle;
      white-space: nowrap;

    }
    .base-title-long {
      width: 65px;
      margin-left: -12px;
    }
    .base-text {
      font-size: 14px;
      color: #222222;
      letter-spacing: 0;
      line-height: 14px;
      display: inline-block;
      width: 248px;
      vertical-align: middle;
    }
    li {
      margin-top: 15px;
    }
    .base-input {
      background: #F9F9F9;
      border: 1px solid #E1E2E7;
      border-radius: 4px;
      padding: 5px 10px;
      font-size: 14px;
      line-height: 14px;
      width: 239px;
    }
  }
</style>





