            //
            produceSymptomsId(data){
                this.produceSymptomsName = this.produceSymptomsList[data].produceSymptomsName;
            },
            //
            produceLevelId(data){
                this.produceLevelName = this.produceLevelList[data].produceLevelName;
            },
            //
            produceSharpenId(data){
                this.produceSharpenName = this.produceSharpenList[data].produceSharpenName;
            },
            //
            produceRemitId(data){
                this.produceRemitName = this.produceRemitList[data].produceRemitName;
            },
            //
            aggravateSymptomsId(data){
                this.aggravateSymptomsName = this.aggravateSymptomsList[data].aggravateSymptomsName;
            },
            //
            aggravateLevelId(data){
                this.aggravateLevelName = this.aggravateLevelList[data].aggravateLevelName;
            },
            //
            aggravateSharpenId(data){
                this.aggravateSharpenName = this.aggravateSharpenList[data].aggravateSharpenName;
            },
            //
            aggravateRemitId(data){
                this.aggravateRemitName = this.aggravateRemitList[data].aggravateRemitName;
            },
            //
            sleepId(data){
                this.sleepName = this.sleepList[data].sleepName;
            },
            //
            spiritId(data){
                this.spiritName = this.spiritList[data].spiritName;
            },
            //
            appetiteId(data){
                this.appetiteName = this.appetiteList[data].appetiteName;
            },
            //
            weightId(data){
                this.weightName = this.weightList[data].weightName;
            },
            //
            excretionId(data){
                this.excretionName = this.excretionList[data].excretionName;
            }
        },
        methods: {
            init() {
                this.userMessage = this.$store.state.currentItem;
                this.initText();
                this.addData();
            },
            addData() {
                let _this = this;
                let dataValue = {
                    caseId: this.userMessage.caseId
                };
                //
                api.ajax({
                    url: XHRList.sickDetailUrl,
                    method: "POST",
                    data: dataValue,
                    beforeSend(config) {
                    },
                    done(res) {
                        if (res.responseObject.responseData.dataList && res.responseObject.responseStatus == true) {
                            let dataList = res.responseObject.responseData.dataList[0];
                            _this.id = dataList.id;
                            _this.produceReason = dataList.produceReason;         //
                            _this.produceSymptomsId = dataList.produceSymptomsId;      //
                            _this.produceLevelId = dataList.produceLevelId;         //
                            _this.produceSharpenId = dataList.produceSharpenId;      //
                            _this.produceRemitId = dataList.produceRemitId;        //

                            if (dataList.produceTreatment != '') {
                                let produceTreatmentList = dataList.produceTreatment.split(",");
                                _this.produceTreatment = produceTreatmentList[0];        //
                                _this.produceTreatment2 = produceTreatmentList[1];       //2
                            }
                            _this.aggravateReason = dataList.aggravateReason;          //
                            _this.aggravateSymptomsId = dataList.aggravateSymptomsId;  //
                            _this.aggravateLevelId = dataList.aggravateLevelId;        //
                            _this.aggravateSharpenId = dataList.aggravateSharpenId;    //
                            _this.aggravateRemitId = dataList.aggravateRemitId;        //

                            if (dataList.aggravateTreatment != '') {
                                let aggravateTreatmentList = dataList.aggravateTreatment.split(",");
                                _this.aggravateTreatment = aggravateTreatmentList[0];    //
                                _this.aggravateTreatment2 = aggravateTreatmentList[1];   //2
                            }
                            _this.sleepId = dataList.sleepId;                  //
                            _this.spiritId = dataList.spiritId;                //
                            _this.appetiteId = dataList.appetiteId;            //
                            _this.weightId = dataList.weightId;                //
                            _this.excretionId = dataList.excretionId;          //

//            $(".J-visualInspection").val(dataListValue.visualInspection);             //
//            $(".J-activityState").val(dataListValue.activityState);                   //
//            $(".J-muscleStrength").val(dataListValue.muscleStrength);                 //

                        }
                    },
                    fail(error){
                        console.log("" + error);
                    }
                });
            },
            saveData() {
                let _this = this;
                let dataValue = {
                    id: this.id,	                                                                    //string		id
                    caseId: this.userMessage.caseId,                                                                                    //string		id
                    produceReason: this.produceReason,                //	string		
                    produceSymptomsId: this.produceSymptomsId,	      //	string		Id
                    produceSymptomsName: this.produceSymptomsName,    //	string		
                    produceLevelId: this.produceLevelId,              //	string		Id
                    produceLevelName: this.produceLevelName,           //	string		
                    produceSharpenId: this.produceSharpenId,	        //	string		Id
                    produceSharpenName: this.produceSharpenName,      //	string		
                    produceRemitId: this.produceRemitId,	            //	string		Id
                    produceRemitName: this.produceRemitName,           //	string		
                    produceTreatment: this.produceTreatment + ',' + this.produceTreatment2,	        //	string		
                    aggravateReason: this.aggravateReason,            //	string		
                    aggravateSymptomsId: this.aggravateSymptomsId,    //	string		Id
                    aggravateSymptomsName: this.aggravateSymptomsName,//	string		
                    aggravateLevelId: this.aggravateLevelId,          //	string		Id
                    aggravateLevelName: this.aggravateLevelName,	    //	string		
                    aggravateSharpenId: this.aggravateSharpenId,	    //	string		Id
                    aggravateSharpenName: this.aggravateSharpenName,	  //	string		
                    aggravateRemitId: this.aggravateRemitId,	        //	string		Id
                    aggravateRemitName: this.aggravateRemitName,    	//	string		
                    aggravateTreatment: this.aggravateTreatment + ',' + this.aggravateTreatment2,	      //	string		
                    sleepId: this.sleepId,	                            //	string		Id
                    sleepName: this.sleepName,	                      //	string		
                    spiritId: this.spiritId,	                        //	string		Id
                    spiritName: this.spiritName,	                    //	string		
                    appetiteId: this.appetiteId,	                    //	string		Id
                    appetiteName: this.appetiteName,	                //	string		
                    weightId: this.weightId,	                        //	string		Id
                    weightName: this.weightName,	                    //	string		
                    excretionId: this.excretionId,	                  //	string		Id
                    excretionName: this.excretionName,	              //	string		
//          visualInspection: this,//	string		
//          activityState: $(".J-activityState").attr("data-val") != "undefined" ? $(".J-activityState").val() : $(".J-activityState").attr("data-val"),	                                                                                //	string		
//          muscleStrength: $(".J-muscleStrength").attr("data-val") != "undefined" ? $(".J-muscleStrength").val() : $(".J-muscleStrength").attr("data-val"),                                //	string		
                    remark: "",	                                                                        //	string		
                    patientId: this.$store.state.patientId	                                    // string		id

                };
                console.log(dataValue);
                api.ajax({
                    url: XHRList.sickSaveUrl,
                    method: "POST",
                    data: dataValue,
                    beforeSend(config) {
                    },
                    done(res) {
                        console.log("");
                        _this.popupShow = true;
                        _this.popupObj = {
                            text: ''
                        }
                    },
                    fail(error){
                        console.log("" + error);
                        _this.popupShow = true;
                        _this.popupObj = {
                            text: ''
                        }
                    }
                });
            },
            initText() {
                let _this = this;
                let dataValue = {
                    caseId: this.userMessage.caseId,
                    patientId: this.userMessage.patientId
                };
                //
                api.ajax({
                    url: XHRList.mainSpeakInfo,
                    method: "POST",
                    data: dataValue,
                    beforeSend(config) {
                    },
                    done(res) {
                        let dataList = res.responseObject.responseData.dataList;
                        _this.treatmentName = dataList[0].treatmentName;
                        _this.illnessName = dataList[0].illnessName;
                        _this.takeMedicine = dataList[0].takeMedicine;
                    },
                    fail(error){
                        console.log("" + error);
                    }
                });
            }
        }
    }
</script>
<style type="text/css" lang="scss" rel="stylesheet/scss" scoped>
    @import "../../scss/library/_common-modules";
    @import "../../scss/record_common";
    .main-sick {
        .main-title {
            width: auto;
            min-width: 52px;
            font-size: 13px;
            color: #555;
            letter-spacing: 0;
            line-height: 14px;
            margin-right: 8px;
            text-align: right;
            display: inline-block;
            white-space: nowrap;
        }
        .main-text {
            font-size: 14px;
            color: #808080;
            letter-spacing: 0;
            line-height: 1.5;
        }
        header {
            position: relative;
            margin-bottom: 30px;
            margin-top: 50px;
            &:before {
                content: "";
                position: absolute;
                top: 50%;
                width: 216px;
                left: 50%;
                margin-left: -108px;
                border: 1px solid #E1E2E7;
            }
        }
        li {
            margin-top: 24px;
        }
        h2 {
            font-size: 12px;
            color: #AAAAAA;
            letter-spacing: 0;
            line-height: 12px;
            margin: 0 auto;
            width: 92px;
            background: #fff;
            text-align: center;
            position: relative;
            z-index: 2;
        }
        select {
            background: #F9F9F9;
            border: 1px solid #E1E2E7;
            border-radius: 4px;
            padding: 5px 10px;
            font-size: 14px;
            line-height: 14px;
            min-width: 84px;
        }
        .select-120 {
            min-width: 119px;
        }
        .main-sick-input {
            background: #F9F9F9;
            border: 1px solid #E1E2E7;
            border-radius: 4px;
            padding: 5px 10px;
            font-size: 14px;
            line-height: 14px;
            width: 235px;
        }
    }
</style>



