<template>
    <article>
        <figure class="messageList-item-content">
            <!---->
            <figure class="messageList-item-img">
                <img :src="$store.state.currentItem.logoUrl" alt="">
            </figure>
            <figcaption class="messageList-item-text">
                <span>{{content}}</span>
                <a href='#' class='link' @click='goToCheck'>""</a>
                <span></span>
                <span v-if="showType==='videoTriage'||showType==='imageTriage'"></span>
            </figcaption>
        </figure>
    </article>
</template>
<script type="text/ecmascript-6">
    /**
     * @Desc
     * @Usage:
     * @Notify
     * @Depend
     *
     * Created by Qiangkailiang on 17/11/1.
     */
    export default{
        data(){
            return {
                content: ""
            }
        },
        mounted(){
            this.contentSwitch(this.showType);
        },
        methods: {
            contentSwitch(type){
                let result = "";
                switch (type) {
                    case "videoTriage":
                    case "imageTriage":
                        result = "";
                        break;
                    case "checkSuggestSendTips":
                        result = "";
                        break;
                }
                this.content=result;
            },
            goToCheck(){
                this.$router.push({
                    path: "/home/majorCheck/",
                    params: {
                        num: JSON.stringify(this.$store.state.currentItem)
                    }
                })
            }
        },
        props: {
            showType: {
                type: String
            }
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss">

</style>



